
import { Toast } from 'vant';

class ValidForm {

    constructor() {

        this.Reg = {
            
            // 手机号
            phone: {
                reg: /^((13[0-9])|(14[5,7])|(15[0-3,5-9])|(17[0,3,5-8])|(18[0-9])|166|198|199|(147))\d{8}$/,
                msg: "手机号格式不正确"
            },

            // 昵称
            nickName: {
                reg: /^([\u4E00-\uFA29]|[\uE7C7-\uE7F3]|[a-zA-Z0-9_]){2,7}$/,
                msg: "昵称格式不正确",
            },

            // 密码
            password: {
                reg: /^\w{6,16}$/,
                msg: "密码格式不正确",
            },

            //旧密码
            oldPassword: {
                reg: /^[a-zA-Z]\w{5,15}$/,
                msg: '旧密码格式不正确且以字母开头'
            },

            //邮箱
            email: {
                reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
                msg: '邮箱格式不正确'
            },

            //6位数字验证码
            code: {
                reg: /^\d{6}$/,
                msg: '验证格式正确'
            }

        }
    }

    // 验证表单
    validateForm(o) {
        // o：验证表单数据
        for(let key in o) {
            if(!this.Reg[key].reg.test(o[key])) {
                Toast(this.Reg[key].msg);

                return false;
            };
        }
        
        //表单验证通过
        return true;
    }

}

export default new ValidForm();