<template>
    <div class="forgot">
        <van-nav-bar
            title="验证"
            left-text="返回"
            left-arrow
            @click-left="$router.go(-1)"
        />

        <van-form>
            <van-field
                v-model="email"
                name="邮箱"
                label="邮箱"
                placeholder="邮箱"
            />

            <van-field
                v-model="code"
                name="验证码"
                label="验证码"
                placeholder="6位验证码"
            >
                <template #button>
                    <van-button size="small" type="info" :disabled="isDisable" @click="send">{{ btnText }}</van-button>
                </template>
            </van-field>

            <div style="margin: 30px 20px;">
                <van-button round block type="info" native-type="submit" @click="verify">
                邮箱验证
                </van-button>
            </div>
        </van-form>
    </div>
</template>

<script>
    import { Toast } from 'vant';
    import validForm from "../assets/js/validForm";

    export default {
        data() {
            return {
                email: "",
                code: "",
                btnText: "发送验证码",
                isDisable: false,
            };
        },

        methods: {
            // 发送验证码
            send() {
                if(!validForm.validateForm({ email: this.email })) {
                    return;
                }

                let time = 60;
                this.btnText = time + "s后重新发送";
                this.isDisable = true;

                let timer = setInterval(() => {
                    if(time == 0) {
                        clearInterval(timer);
                        timer = null;
                        this.btnText = "发送验证码";
                        this.isDisable = false;
                    }else {
                        time--;
                        this.btnText = time + "s后重新发送";
                    }
                }, 1000);

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: "/emailValidCode",
                    data: {
                        appkey: this.appkey,
                        email: this.email,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    Toast(res.data.msg);

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 验证验证码
            verify(){
                if(!validForm.validateForm({ email: this.email, code: this.code })) {
                    return;
                }

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: "/checkValidCode",
                    data: {
                        appkey: this.appkey,
                        validCode: this.code,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == "K001") {

                        this.$router.push({ name: "Retrieve" });

                    }else{

                        Toast(res.data.msg);

                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });

            }
        }
    }
</script>

<style lang="less" scoped>
    .van-nav-bar{
        height: 45px;
    }
    .van-form{
        margin-top: 50px;

        
    }
</style>